* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Poppins;
}

.nav > img{
    height: 100%;
}

.nav{
    box-shadow: 0 1px 5px  rgb(179, 177, 177);
    padding: 16px 32px 16px 32px;
    height: 70px;
}

.hero{
    display: flex;
    flex-direction: column;
    margin-bottom: 32px;
}

.hero > p{
    margin-right: auto;
    max-width: 310px;
}

.hero > h1{
    font-size: 3rem;
    margin-bottom: 16px;
}
main{
    padding: 42px;
}
.hero img{
    width: 70%;
    max-width: 500px;
    align-self: center;
}
.card-image{
    width: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    aspect-ratio: 1/1.3;
    overflow: hidden;
    position: relative;
    border-radius: 8px;
    margin-bottom: 8px;
}
.card-image > img{
    width: 100%;
    height: 100%;
}
.card-image > span{
    background-color: white;
    position: absolute;
    border-radius: 3px;
    padding: 2px;
    top: 8px;
    left: 8px;
}
.rating{
    display: flex;
    gap: 3px;
    align-items: center;
}
.card-info{
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.card{
    height: fit-content;
    padding: 3px;

}
.rating-region{
    color: #918E9B;
}
.card-list{
    width: 80%;
    overflow-x: scroll;
    gap: 20px;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    scrollbar-width: 10px;
    scrollbar-color: red;
}

@media (max-width:340px){
    .hero h1{
        font-size: 2rem;
    }
}